.wrapper {
    display: flex;
    justify-content: center;
}

.buttonWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 90%;
    gap: 10px;
}

.button {
    cursor: pointer;
    width: 100%;
    height: 74px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 31px;
    font-weight: 700;
    border-radius: 15px;
    color: var(--button-color);

    position: relative;
    overflow: hidden;
}

.buttonHalf {
    width: calc(50% - 5px);
}

.buttonBg {
    display: flex;
    background: black;
    align-items: center;
    justify-content: center;
    width: calc(100% - 8px);
    height: calc(100% - 8px);
    position: relative;
    border-radius: 15px;
}

.button::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    border-radius: 15px;
    background: linear-gradient(270deg, #216efd 0%, #ff2142 100%);
}
